@import '../../../styles/vars';
/* (375, 768, 1440) */

.card {
    background-color: #f7f6f9;
    padding: 14px;
    border: 1px solid rgba(220, 227, 229, 0.8);
    border-radius: 8px;
}

.title {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    margin-bottom: 32px;
    text-align: left;
    font-size: 14px;
    color: #111111;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    // &::after{
    // content: '...';
    // font-size: 14px;
    // // position: absolute;
    // // right: 0;
    // // bottom: 0;
    // }

    // &::first-line{
    //   font-size: 14px;
    // }
}

.block {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px;
}

.user {
    display: flex;
    // flex-direction: row;
    align-items: flex-end;
}

.iconAvatar {
    margin-right: 8px;
    box-sizing: border-box;
    width: 32px;
    height: 32px;
    border: 1.8px solid #3e85f3;
    border-radius: 50%;
}

.userAvatar {
    margin-right: 8px;
    box-sizing: border-box;
    width: 32px;
    height: 32px;
    border: 1.8px solid #3e85f3;
    border-radius: 50%;
}

.priority {
    padding: 4px 12px;
    border-radius: 4px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #f7f6f9;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;

    &.low {
        background-color: #72c2f8;
    }

    &.medium {
        background-color: #f3b249;
    }

    &.high {
        background-color: #ea3d65;
    }
}